@import "component/dcm-variables";
@import "component/dcm-mixins";

// Note: !important used to override other css file.

.track-content {
	.track-row {
		padding: 10px;
		position: relative;
		border-left: 2px solid $blue-color;
		margin: 0 0 30px;
		> h2 {
			background-color: $blue-color;
			color: $white-color;
			padding: 10px;
			position: relative;
			&:before {
			    content: "";
			    height: 2px;
			    width: 100%;
			    display: block;
			    background-color: $white-color;
			    left: 0;
			    top: 3px;
			    position: absolute;
			}
			&:after {
			    content: "";
			    height: 100%;
			    width: 2px;
			    display: block;
			    background-color: $white-color;
			    left: 5px;
			    top: 0;
			    position: absolute;
			}
		}
		.session-data {
			padding: 10px;
		}
	}
}